:root {
  --color: #111111;
  --font-family-sans-serif: "Helvetica Neue", Arial, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  --font-family-monospace: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-family: "Helvetica Neue", Arial, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-family: var(--font-family-sans-serif);
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -ms-overflow-style: 'scrollbar';
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

html, body {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  display: flex;
}

@-ms-viewport {
  width: device-width;
}

article, aside, figcaption, figure, footer, header, hgroup, main, nav, section {
  display: block;
}

a {
  color: #C678DD;
}

a:hover {
  color: #111111;
}

code {
  background: none;
  font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.5;
  -moz-tab-size: 4;
  tab-size: 4;
  -webkit-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
}

#root {
  display: flex;
  width: 100%;
  flex-direction: column;
}

.container {
  display: flex;
  width: 100%;
  max-width: 700px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  text-align: center;
}

.intro {
  min-height: 100vh;
}

table {
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 1rem;
  background-color: transparent;
  font-size: 14px;
  line-height: 1.45;
}

p code,
table code {
  padding: 2px 4px;
  font-size: 90%;
  color: #111111;
  background-color: #f1f1f1;
  border-radius: 4px;
}

table th,
table td {
  padding: 0.75rem;
  vertical-align: top;
  border-top: 1px solid #dee2e6;
}

table thead th {
  vertical-align: bottom;
  border-bottom: 2px solid #dee2e6;
}

/* Text component */
.text {
  color: var(--color);
  font-size: 18px;
  margin: 18px 0;
  line-height: 1.45;
}

.text__inherit {
  font-size: inherit;
  line-height: inherit;
}

/* PrismJS 1.15.0
https://prismjs.com/download.html#themes=prism-okaidia&languages=markup+css+clike+javascript+jsx+typescript+tsx&plugins=command-line */
/**
 * okaidia theme for JavaScript, CSS and HTML
 * Loosely based on Monokai textmate theme by http://www.monokai.nl/
 * @author ocodia
 */

code[class*="language-"],
pre[class*="language-"] {
	color: #ABB2BF;
	background: none;
	text-shadow: 0 1px rgba(0, 0, 0, 0.3);
	font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
	text-align: left;
	white-space: pre;
	word-spacing: normal;
	word-break: normal;
	word-wrap: normal;
	line-height: 1.5;

	-moz-tab-size: 4;
	tab-size: 4;

	-webkit-hyphens: none;
	-ms-hyphens: none;
	hyphens: none;
}

/* Code blocks */
pre[class*="language-"] {
	padding: 1em;
	margin: .5em 0;
	overflow: auto;
	border-radius: 0.3em;
}

:not(pre) > code[class*="language-"],
pre[class*="language-"] {
	background: #282C34;
}

/* Inline code */
:not(pre) > code[class*="language-"] {
	padding: .1em;
	border-radius: .3em;
	white-space: normal;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
	color: slategray;
}

.token.punctuation {
	color: #f8f8f2;
}

.namespace {
	opacity: .7;
}

.token.property,
.token.tag,
.token.constant,
.token.symbol,
.token.deleted {
	color: #f92672;
}

.token.boolean,
.token.number {
	color: #ae81ff;
}

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
	color: #6EBA76;
}

.token.entity,
.token.url,
.language-css .token.string,
.style .token.string,
.token.variable {
	color: #f8f8f2;
}

.token.class-name {
	color: #E5C07B;
}

.token.function {
	color: #61AFEF;
}

.token.atrule,
.token.attr-value {
	color: #e6db74;
}

.token.operator {
	color: #4CB6C2;
}

.token.keyword {
	color: #C678DD;
}

.token.regex,
.token.important {
	color: #fd971f;
}

.token.important,
.token.bold {
	font-weight: bold;
}
.token.italic {
	font-style: italic;
}

.token.entity {
	cursor: help;
}

.command-line-prompt {
	border-right: 1px solid #999;
	display: block;
	float: left;
	font-size: 100%;
	letter-spacing: -1px;
	margin-right: 1em;
	pointer-events: none;

	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.command-line-prompt > span:before {
	color: #999;
	content: ' ';
	display: block;
	padding-right: 0.8em;
}

.command-line-prompt > span[data-user]:before {
	content: "[" attr(data-user) "@" attr(data-host) "] $";
}

.command-line-prompt > span[data-user="root"]:before {
	content: "[" attr(data-user) "@" attr(data-host) "] #";
}

.command-line-prompt > span[data-prompt]:before {
	content: attr(data-prompt);
}

/* Title component */
.title {
  color: var(--color);
  margin: 0 auto 16px;
}

.title__h1 {
  font-size: 44px;
  line-height: 1.2;
}

.title__h2 {
  font-size: 38px;
  line-height: 1.2;
}

.title__h3 {
  font-size: 34px;
  line-height: 1.2;
  font-weight: 400;
}

.title__h4 {
  font-size: 24px;
  line-height: 1.2;
  font-weight: 400;
  color: #666666;
}

.title__h5 {
  font-size: 20px;
  line-height: 1.2;
}

.title__h6 {
  font-size: 18px;
  line-height: 1.2;
}

/* Anchor component */
.anchor {
  display: block;
  float: left;
  vertical-align: middle;
  line-height: 1.45;
  margin-left: -30px;
  padding-right: 4px;
  visibility: hidden;
}

/* Button component */
.button {
  border: 1px solid transparent;
  border-radius: 4px;
  box-shadow: 0 1px 1px rgba(0,0,0,.084);
  cursor: pointer;
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
  text-align: center;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.button__default {
  display: block;
  height: 44px;
  padding: 0 19px;
  font-size: 15px;
  line-height: 41px;
}

.button_small {
  /* todo */
}

.button__large {
  line-height: 1.6;
  height: 60px;
  padding: 14px 24px;
  font-size: 18px;
}

.button__primary {
  color: #fff;
  background-color: #111;
}

.button__primary:hover {
  background-color: #000;
}

.button__secondary {
  color: #fff;
  background-color: #3cb46e;
}

.button__secondary:hover {
  background-color: #37a866;
}

.github-corner {
  position: fixed;
  top: 0;
  right: 0;
}

.github-corner:hover .octo-arm {
  -webkit-animation: octocat-wave 560ms ease-in-out;
  animation: octocat-wave 560ms ease-in-out;
}

@-webkit-keyframes octocat-wave {
  0%, 100% {
    -webkit-transform: rotate(0);
            transform: rotate(0)
  }
  20%, 60% {
    -webkit-transform: rotate(-25deg);
            transform: rotate(-25deg)
  }
  40%, 80% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg)
  }
}

@keyframes octocat-wave {
  0%, 100% {
    -webkit-transform: rotate(0);
            transform: rotate(0)
  }
  20%, 60% {
    -webkit-transform: rotate(-25deg);
            transform: rotate(-25deg)
  }
  40%, 80% {
    -webkit-transform: rotate(10deg);
            transform: rotate(10deg)
  }
}

@media (max-width: 500px) {
  .github-corner:hover .octo-arm {
    -webkit-animation: none;
            animation: none;
  }

  .github-corner .octo-arm {
    -webkit-animation: octocat-wave 560ms ease-in-out;
            animation: octocat-wave 560ms ease-in-out;
  }
}

/* Rule component */
.rule {
  border-top: 1px solid #DDDDDD;
  margin-top: 24px;
  margin-bottom: 24px;
}

/* Section component */
.section {
  font-size: 16px;
  padding-top: 2em;
  padding-bottom: 2em;
  line-height: 1.45;
}

